import {useEffect, useState} from 'react'
import {KTSVG} from '../../../_metronic/helpers'

const Kpit = (props: any) => {
  const [volumeold, setvolumeold] = useState<any>('')
  const [percentageold, setpercentageold] = useState<any>('')

  const {
    disableInput,
    percentage,
    setpercentage,
    volume,
    setvolume,
    EditClick,
    SaveClick,
    sigmaPCA,
    SetdisableInput,
  } = props

 useEffect(() => {
    let volumnold1 = localStorage.getItem('kpitvolume'),
      percantageold1 = localStorage.getItem('kpitpercentage')
    setvolumeold(volumnold1)
    setpercentageold(percantageold1)
  }, [])

  return (
    <form >
      <div className='row  g-xl-10 '>
        <div className='col-xl-11 col-sm-6 col-md-10'>
          <div className='card-xxl-stretch mb-4 mb-xxl-7'>
            <div className='row gy-5 g-xl-7'>
              <div className={`card`}>
                <div className='card-header border-0 p-2' style={{minHeight: '40px'}}>
                  <div className='input-group input-group-sm mb-1'>
                    <span
                      className='input-group-text'
                      id='inputGroup-sizing-sm'
                      style={{backgroundColor: 'white'}}
                    >
                      Target Percentage(%)
                    </span>
                    {disableInput === 'true' ? (
                      <input
                        required
                        type='number'
                        className='form-control form-control-solid quantity'
                        aria-label='Sizing example input'
                        aria-describedby='inputGroup-sizing-sm'
                        value={percentage}
                        autoFocus={true}
                        onChange={(e: any) => {
                          setpercentage(e.target.value)

                          if (
                            volumeold == null ||
                            volumeold == '' ||
                           
                            volumeold == undefined
                            ||
                            isNaN(volumeold)
                          ) {
                            setvolume((e.target.value * sigmaPCA) / 100)
                          } else {
                            var d = percentageold / e.target.value

                            var volumenew = (volumeold / d).toFixed(2)

                            setvolume(volumenew)
                          }
                        }}
                        style={{opacity: 1}}
                      />
                    ) : (
                      <span className='form-control'>{percentage}</span>
                    )}
                    <span
                      className='input-group-text'
                      id='inputGroup-sizing-sm'
                      style={{backgroundColor: 'white'}}
                    >
                      Target Volume
                    </span>

                    {disableInput == 'true' ? (
                      <input
                        required
                        type='number'
                        className='form-control form-control-solid'
                        aria-label='Sizing example input'
                        aria-describedby='inputGroup-sizing-sm'
                        value={volume}
                        onChange={(e: any) => {
                          if (
                            (percentageold !== null && volumeold !== null && isNaN(percentageold)) ||
                            (percentageold !== '' && volumeold !== '' && isNaN(percentageold))
                          ) {
                            let percentagenew = Math.round(
                              (percentageold * e.target.value) / volumeold
                            ).toFixed(2)
                            setpercentage(percentagenew)
                          } else {
                            let percentagenew = Math.round(
                              (e.target.value / sigmaPCA) * 100
                            ).toFixed(2)
                            setpercentage(percentagenew)
                          }

                          setvolume(e.target.value)
                        }}
                        onKeyPress={(event) => {
                          if (!/[0-9]/.test(event.key)) {
                            event.preventDefault()
                          }
                        }}
                      />
                    ) : (
                      <span className='form-control'>{volume}</span>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className='col-xl-1 col-sm-6 col-md-2 '>
          <div className='row gy-5 g-xl-7'>
            <div className='border-0 pt-1' style={{minHeight: '40px'}}>
              {disableInput == 'false' ? (
                <div
                  data-bs-toggle='tooltip'
                  data-bs-placement='top'
                  data-bs-trigger='hover'
                  title='Edit'
                >
                  <button
                    type='button'
                    className='btn btn-icon btn-warning fa-pencil'
                    onClick={EditClick}
                    tabIndex={5}
                  >
                    <KTSVG
                      path='/media/icons/duotune/arrows/arr036.svg'
                      className='svg-icon svg-icon-3'
                    />
                  </button>
                </div>
              ) : (
                <div
                  data-bs-toggle='tooltip'
                  data-bs-placement='top'
                  data-bs-trigger='hover'
                  title='Save'
                  className='symbol symbol-50px'
                >
                  <button
                    type='button'
                    className='btn btn-icon btn-success '
                    onClick={SaveClick}
                    tabIndex={4}
                  >
                    <KTSVG
                      path='/media/icons/duotune/arrows/arr012.svg'
                      className='svg-icon svg-icon-3'
                    />
                  </button>
                  <span
                    className='symbol-badge badge badge-circle bg-danger start-100 cursor-pointer'
                    onClick={(e:any) => {
                      SetdisableInput('false')
                    }}
                    data-bs-toggle='tooltip'
                    data-bs-placement='top'
                    data-bs-trigger='hover'
                    title='Cancel'
                  >
                    <i className='fa fa-times' aria-hidden='true'></i>
                  </span>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </form>
  )
}

export default Kpit
